<template>
  <div class="bar-update">
    <div v-if="updateList.length" class="bar-update">
      <div class="update-item" v-for="(item, index) in updateList" :key="index">
        <div class="item-date flex-row">
          <img :src="`${$cdnImageUrl}/shop/icon_shop_prod_new.png`"/>
          <span>
            {{item.dateStr}}上新
          </span>
        </div>
        <public-product-list :productList="item.productList" :switchStyle='true' :visitStore="storeId"/>
      </div>
      <div class="no-more">上新商品没有啦，去看看别的吧~</div>
    </div>
    <common-empty v-if="fetchEnd && updateList.length === 0"/>
  </div>
</template>
<script>
import commonEmpty from '../commonEmpty';
import publicProductList from '@/components/publicProductList';
import { getStoreNewProducts } from '@/http/service/shop';
import { mapGetters } from 'vuex';
/** 千店千面 头部tabbar 【上新】 */
export default {
  props: {
    storeId: {
      type: String,
      default: '',
    }
  },
  components: {
    commonEmpty,
    publicProductList,
  },
  data() {
    return {
      fetchEnd: false,
      updateList: [],
    }
  },
  mounted() {
    this.getUpdateList();
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  watch: {
    mId() {
      this.getUpdateList();
    }
  },
  methods: {
    goProduct(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${this.storeId || ''}`);
    },
    getUpdateList() {
      getStoreNewProducts({
        storeId: this.storeId,
        streetId: this.location.townshipId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,

      }).then(res => {
        if (res.success) {
          if (res.data) {
            console.log('上新接口：', res.data);
            for (const item of res.data) {
              const productList = [];
              item.dateList && item.dateList.map(prod => {
                productList.push({
                  comments: '',
                  defaultImageUrl: prod.defaultImageUrl,
                  finalPrice: prod.storePrice || '',
                  goodRate: '',
                  productFullName: prod.productName || '',
                  productId: prod.productId,
                  productTitle: prod.productName || '', // 副标题
                  sku: prod.sku,
                  tags: prod.label || '',
                  sellPoint: prod.sellPoint,
                  productActNew: '', // 活动
                  productActTerm: '', //
                  plusPrice: prod.plusPrice,
                  isLowestFlash: !!prod.lowestFlashPrice
                });
              });
              item.productList = productList;
            }
          }
          this.updateList = res.data;
          this.fetchEnd = true
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
          this.fetchEnd = true
        }
      });
    }
  }
}
</script>
